body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
    --principal-color: #4caf50;
    --secondary-color: #000;
    --third--color: #121212;
    --principal-font: "Montserrat", sans-serif;
    --hover-background: rgba(62, 62, 62, 0.5)
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-image: url(/SpotiSearch/static/media/Background2.4de1daf1.jpg);
    background-position: right;
    background-size: auto;
    background-repeat: no-repeat;
}
.form {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width: 90%;
    height: 30vh;
    border: 1px solid black;
    margin: 20px auto;
    background-color: var(--third--color);
    border-radius: 20px;
}

.form--label {
    font-family: var(--principal-font);
    font-size: 20px;
    color: #fff;
}

.form--input {
    font-family: var(--principal-font);
    font-size: 20px;
    text-align: center;
    width: 80%;
    display: block;
    margin: 0 auto;
    padding: 10px 20px;
    border-radius: 50px;
    border: 2px solid var(--secondary-color);
}

.form--button {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px 30px;
    background-color: var(--principal-color);
    color: var(--secondary-color);
    border-radius: 500px;
    border: 1px solid #4e4e4e;
    font-family: var(--principal-font);
    font-weight: bold;
    font-size: 20px;
    cursor: pointer;
}

@media only screen and (min-width: 700px) {
    .form {
        width: 60%;
        height: 40vh;
    }
}
.navbar {
    background-color: var(--secondary-color);
    padding: 15px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
}

.navbar-brand {
    margin-left: 5px;
    font-family: var(--principal-font);
    font-weight: bold;
    font-size: 25px;
    color: var(--principal-color);
}
.songCard {
    display: flex;
    width: 70%;
    background-color: transparent;
    margin: 7px auto;
    padding: 3px;
    cursor: pointer;
}

.songCard--info {
    font-family: var(--principal-font);
    color: #fff;
    display: flex;
    flex-direction: column;
}

.songCard--cover {
    margin-right: 10px;
}

.card--info--title {
    font-size: 18px;
    font-weight: bold;
}

.card--info--artist {
    font-size: 13px;
}

img {
    height: 50px;
}

.songCard:hover {
    background-color: var(--hover-background);
}
.cardContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    background-color: #12121283;
}

@media only screen and (min-width: 700px) {
    .cardContainer {
        width: 50%;
        margin: 0 auto;
    }
}
a .next {
    display: inline-block;
    background-size: cover;
}

a .prev {
    rotate: 180deg;
    display: inline-block;
    background-size: cover;
}

a .pageButton:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    cursor: pointer;
}

a {
    text-decoration: none;
    color: var(--principal-color);
}

ion-icon {
    cursor: pointer;
}

.pageButton {
    height: 32px;
}
.detailCard {
    position: fixed;
    top: 25%;
    left: 15%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width: 70%;
    background-color: rgba(12, 12, 12, 0.9);
    padding: 10px;
    border-radius: 20px;
    border: 2px solid var(--secondary-color);
    color: #fff;
    font-family: var(--principal-font);
    text-transform: capitalize;
}

.detailCard--cover {
    border-radius: 10px;
    background-color: rebeccapurple;
    height: 200px;
    width: 200px;
}

.detailCard--title {
    font-size: 25px;
    font-weight: bold;
    text-align: center;
}

.detailCard--artist {
    font-size: 18px;
    font-weight: 400;
}

.detailCard--albumType {
    font-size: 18px;
    font-weight: 400;
}

.detailCard--releaseDate {
    font-size: 18px;
    font-weight: 400;
}

.hide {
    display: none;
}

iframe {
    width: 80%;
    height: 40px;
    border-radius: 15px;
}

@media only screen and (min-width: 700px) {
    .detailCard {
        width: 30%;
        left: 35%;
    }
}
.buttonsContainer {
    height: 50px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    color: #fff;
}

@media only screen and (min-width: 700px) {
    .buttonsContainer {
        margin: 0 auto;
        width: 50%;
        height: 5rem;
    }
}
